* {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;;
  color: white;
}

.bannerContainer {
  height: 100vh;
  background-color: rgb(19, 19, 19);
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bannerContainer h1 {
  user-select: none;
  font-size: 100px;
  animation: neon 1.5s ease-in-out infinite alternate;
}

@keyframes neon {
  from {
    text-shadow: 0px 0px 10px rgba(116, 121, 255, 0.214);
  }
  to {
    text-shadow: 0px 0px 100px rgb(116, 121, 255);
  }
}

.scrollDownContainer {
  position: absolute;
  bottom: 0;
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}

.scrollDownContainer .mouse {
  width: 2.5rem;
  height: 5rem;
  /* background-color: white; */
  border: 4px solid white;
  border-radius: 2rem;
  display: flex;
}

.scrollDownContainer .mouse span {
  width: 1rem;
  height: 1rem;
  background-color: white;
  display: block;
  border-radius: 50%;
  margin: auto;
  animation: move-wheel .5s linear infinite;
}

.scrollDownContainer .arrow span {
  display: block;
  width: 1rem;
  height: 1rem;
  border: 4px solid transparent;
  border-right-color: white;
  border-bottom-color: white;
  transform: rotate(45deg);
  margin-bottom: .5rem;
  animation: arrow-down 1s alternate infinite;

}

@keyframes move-wheel {
  from {
    opacity: 0;
    transform: translateY(-1rem);
  }
  to {
    opacity: 1;
    transform: translateY(1rem);
  }
}

@keyframes arrow-down {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.photoDisplayContainer {
  background-color: black;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: column;
}

.filterZone {
  font-size: 20px;
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
  width: 80%;
  height: 50px;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
}

.filterButton {
  padding: 0;
  background-color: rgb(3, 0, 26);
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  align-content: center;
  transition: background ease-in .5s;
}

.filterButton:hover {
  background-color: rgb(0, 102, 255);
}

.filterButton.active {
  background-color: rgb(0, 102, 255);
}

.photoDisplayZone {
  width: 80%;
  background-color: rgba(33, 25, 79, 0.488);
  margin-bottom: 50px;
}

.photoDisplayCadre {
  padding: 10px;
  /* width: 50%; */
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.photoDisplayZone img {
  width: calc(50% - 20px);
  padding: 10px;
  -webkit-user-drag: none;
  pointer-events: none;
}

.footer {
  background-color: rgb(3, 0, 26);
  height: 200px;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}


@media only screen and (max-width : 321px) {
  .bannerContainer h1 {
    font-size: 30px;
  }

  .photoDisplayZone {
    width: 100%;
  }

  .photoDisplayZone img {
    width: calc(100% - 20px);
  }

  .filterZone {
    width: 100%;
    font-size: 5px;
  }

  .filterButton {
    padding: 10px 0;
  }

  .footer {
    font-size: 8px;
  }
}

/* Smartphones (landscape) ----------- */
@media only screen and (min-width : 321px) {
  .bannerContainer h1 {
    font-size: 40px;
  }
  .photoDisplayZone {
    width: 100%;
  }

  .filterZone {
    width: 100%;
    font-size: 5px;
  }

  .filterButton {
    padding: 10px 0;
  }

  .photoDisplayZone img {
    width: calc(100% - 20px);
  }

  .footer {
    font-size: 10px;
  }
}
    
